import BaseApiService from "../BaseApiService";

const DEFAULT_PATH = "/custom/api/pd";
const COMMON_PATH = "/custom/api";

class PDService extends BaseApiService {
  // ------------------------ To get PD details --------------------------- //
  getPDDetails = (id: string) => {
    const endPoint = DEFAULT_PATH + `/search?stage.uid=${id}&isActive=true`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------ To update PD  details --------------------------- //
  updatePDDetails = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/detail`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // ------------------------ To update PD writeUp details --------------------------- //
  updateWriteUpDetails = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/detailed/writeup`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // ------------------------ To update PD family details --------------------------- //
  updateFamilyDetails = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/family/detail`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // ------------------------ To update PD other income details --------------------------- //
  updateOtherIncomeDetails = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/other/income`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // ------------------------ To update PD investment details --------------------------- //
  updateInvestmentDetails = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/investment/details`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // ------------------------ To update PD repayment history --------------------------- //
  updateRepaymentHistory = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/repayment/details`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // ------------------------ To get PD employment detail --------------------------- //
  getEmploymentDetail = (loanId: string, stageId: string) => {
    const endPoint = DEFAULT_PATH + `/employmentdetail/${loanId}/${stageId}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------ To create PD employment detail --------------------------- //
  createEmploymentDetail = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/employmentdetail`;
    return this.makePostRequestWithAuth(endPoint, payload);
  };

  // ------------------------ To update PD employment detail --------------------------- //
  updateEmploymentDetail = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/employmentdetail`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // ------------------------ To get PD reference detail --------------------------- //
  getReferenceDetail = (stageId: string) => {
    const endPoint =
      COMMON_PATH +
      `/employment/reference/search?stage.uid=${stageId}&limit=1000&employmentDetail.uid:null=''&isActive=true`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------ To create PD reference detail --------------------------- //
  createReferenceDetail = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/rental/income/reference`;
    return this.makePostRequestWithAuth(endPoint, payload);
  };

  // ------------------------ To update PD reference detail --------------------------- //
  updateReferenceDetail = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/rental/income/reference`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  completeStage = (id: string) => {
    const endPoint = DEFAULT_PATH + `/complete/${id}`;
    return this.makePutRequestWithAuth(endPoint);
  };

  search = (search: string) => {
    const endPoint = DEFAULT_PATH + `/search${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
}

export default new PDService();
